<template>
  <div>
    <div>
       <nav>
        <div class="nav-wrapper">
          <a href="#" class="brand-logo center">{{ nombre }} Installer</a>
        </div>
      </nav>
    </div>

    <div v-if="loading == 'no'"> 
    <center>
		<div style="margin: 10px 0;" ></div>  
      <button v-on:click="IAD('instalar')" class="waves-effect waves-light btn btn-large" >Install</button>
		<div style="margin: 10px 0;" ></div>
      <button v-on:click="IAD('update')" class="waves-effect waves-light btn btn-large" >Update</button>
		<div style="margin: 10px 0;" ></div>
      <button v-on:click="IAD('desinstalar')" class="waves-effect waves-light btn btn-large" >Uninstall</button>
    </center>
    </div>
    <div v-else-if="loading == 'si'"> 
      <div class="progress">
        <div class="indeterminate"></div>
      </div>
      <center>
        <p>Wait a moment...</p>
        <div class="preloader-wrapper active">
          <div class="spinner-layer spinner-red-only">
            <div class="circle-clipper left">
              <div class="circle"></div>
              </div><div class="gap-patch">
              <div class="circle"></div>
              </div><div class="circle-clipper right">
            <div class="circle"></div>
          </div>
          </div>
        </div>
      </center>
    </div>
    <!-- Resultado... -->
    <div v-else-if="loading == 'r'" >
      <center>
		    <div style="margin: 10px 0;" ></div>
		      <div class="card-panel teal lighten-2 container"><h5>{{resultado}}</h5></div>
     	    <div style="margin: 10px 0;" ></div>
		    <p v-on:click="IAD('cerrar')" ><a class="waves-effect waves-light btn btn-large">Close</a></p>		
      </center>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  props: ['nombre'],
  data() {
    return {
      loading: "no",
      resultado: ""
    };
  },
  methods: {
    IAD(clc) {
      this.loading = "si";
      axios
        .post("http://localhost:8087/" + clc + "/us")
        .then(response => {
          this.loading = "r";
          this.resultado = response.data;
        })
        .catch(e => {
          this.loading = "r";
          this.resultado = "Total failure :C";
        });
    }
  }
};
</script>



