<template>
  <div>
    <Sl v-if="live" v-bind:nombre="name" ></Sl>
    <div v-else>
	<center>
        <p>Wait a moment...</p>
        <div class="preloader-wrapper active">
          <div class="spinner-layer spinner-red-only">
            <div class="circle-clipper left">
              <div class="circle"></div>
              </div><div class="gap-patch">
              <div class="circle"></div>
              </div><div class="circle-clipper right">
            <div class="circle"></div>
          </div>
          </div>
        </div>
      </center>
	</div>
  </div>
</template>

<script>
import Sl from "./components/select-es-us";
import axios from "axios";

export default {
  name: "App",
  components: {
    Sl
  },
  data() {
    return {
      name: '',
      live: false
    };
  },
  created() {
    axios
      .get("http://localhost:8087/live")
      .then(response => {
		this.name = response.data
		this.live = true;
      })
      .catch(e => {
		alert('Error se esperava la respuesta del cliente :' + e)
      });
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



